import React from 'react'

function Skills() {
  const skills=[
    {logo:'logo-html5',level:'Advanced',name:'Html5',count:90},
    {logo:'logo-wordpress',level:'Intermediate',name:'Wordpress',count:60},
    {logo:'logo-css3',level:'Advanced', name:'Css3',count:80},
    {logo:'logo-laravel', level:'Advanced' ,name:'Laravel',count:88},
    {logo:'logo-javascript',level:'Intermediate', name:'JavaScript', count:70},
    {logo:'logo-react',level:'Advanced', name:'React Js',count:85},
     ]
  return (
    <section id='skills'  className='py-10 bg-blue-950 relative ' >
      <div className='mt-8 text-gray-100 text-center'>
       <h3 className='text-4xl font-semibold'>My <span className='text-cyan-600'>Skills</span>
       </h3>
       <p className=' text-gray-400 mt-3 text-lg '>My knowledge</p>
       <div className='flex items-center justify-center mt-12 gap-10 flex-wrap'>
        {skills.map((skill,count)=>{
          return(
            <div  key={count}className='border-2  group border-cyan-600 relative min-w-[10rem] max-w-[16rem] bg-gray-900 p-10 rounded-xl'>
<div
style={{
  background: `conic-gradient(rgb(8,145,170) ${skill.count}%,#ddd ${skill.count}%)`,
}}
 className='w-32 h-32 flex items-center justify-center  rounded-full'>
   
  <div className='text-6xl h-28 w-28  group-hover:text-cyan-600  flex items-center justify-center  bg-gray-900 rounded-full  ' > 
   
    <ion-icon name={skill.logo}></ion-icon>
   
  </div>
 

</div> 
<p className='text-xl mt-3'>{skill.level}</p>

        </div>
          )
        })}
       </div>
    </div>
    </section>
    
  )
}

export default Skills
