import React from 'react'
import about from '../assets/photo/about.png'

function About() {
  const  information=[
    {text:'Yers Exeperience',Nombre:'02'},
    {text:'Completed Projects',Nombre:'10'},
    {text:'Companies Work ',Nombre:'1'}
  ]
  return (
    <section id='about' className='py-10 text-white'> 
      <div className='text-center mt-8 '>
        <h3 className='text-4xl font-semibold'>
          About<span className='text-cyan-600'>Me</span>
        </h3>
        <p className='text-gray-400 my-3 text-lg'>My Introduction</p>
    <div className='flex md:flex-row flex-col-reverse items-center  md-gap-6 gap-12 px-10 max-w-6xl mx-auto'>
      <div className='p-2 md:mr-8' >
        <div className='text-gray-300  my-3'>
          <h4 className='text-justify   leading-7 font-bold mx-auto w-11/12'>
          I am  <span className='text-cyan-600'> Youssef Hanouch</span>  &nbsp;
           a passionate full-stack web developer with a focus on creating
           interactive websites. With expertise in both front-end and back-end technologies, 
            I turn ideas into reality. Explore my projects that  showcase my creativity and commitment to excellence. Always seeking new challenges,
           I am open to collaboration opportunities.
            Discover my portfolio and my passion for web 
            development. Thank you for visiting, feel free
             to reach out to me to discuss exciting projects.
          </h4>
          <div className='flex mt-10 items-center gap-7 '>
            {
              information.map((cnt,e)=>{
                return(
               <div key={e}>
                <h3 className='md:text-4xl text-2xl font-semibold text-white'>{cnt.Nombre} <span>+</span>{''} </h3>
                <span className='text-cyan-600'>{cnt.text}</span>
               </div>)
              })
            }
            </div>
            <br />
            <a href="https://drive.google.com/file/d/14QwJ4--YrRKBS6ymDGBJI5WUOXFpXJCj/view?usp=drive_link" target='__blank' download  >
            <button className='btn-primary '>Download CV</button>
            </a>
           
            
          
        </div>
      </div>
      <div className='flex-1 flex justify-center items-center'>
       <div className='lg:w-96 h-full relative sm:w-10/12 w-11/12  imageAbout'>
      
        <img src={about} alt="not found" className='w-full object-cover bg-sky-100 rounded-xl ' />
      </div>
      </div>
      </div>
      </div>
    </section>
    
  )
}

export default About
