import React, { useState } from 'react';
import './Popup.css'; // Import CSS for styling
import ReactPlayer from 'react-player';

function Popup(props) {
  const [isOpen, setIsOpen] = useState(false);

  const togglePopup = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div>
      <button className="text-cyan-600 btn-primary  bg-gray-800 px-2 py-1 inline-block"  onClick={togglePopup}>{props.item}</button>

      {isOpen && (
        <div className="popup">
          <div className="popup-inner">
            <button style={{color:'red'}} className="close-button" onClick={togglePopup}>
              Close
            </button>
            <div className="popup-content">
             <button></button>
              <p>This is the content of the pop-up.</p>
            </div>
            
            {/* Embed the video player */}
            <div className="video-container">
            
      <video controls width="720" height="480">
        <source src={props.vedeo} type="video/mp4" />
        Votre navigateur ne prend pas en charge la balise vido.
      </video>
    </div>
       

          </div>
        </div>
      )}
    </div>
  );
}

export default Popup;
