import React from 'react'


function Contact() {
  const contact_information=[
    {logo:'mail',link:'mailto:youssefhanouch8@gmail.com',text:'Email'},
    {logo:'logo-whatsapp',link:"https://wa.me/0618313352",text:'whatsapp'},
    {logo:'logo-instagram',link:'https://www.instagram.com/youssef.hanouch/',text:'instagram'},
    {logo:'logo-github',link:'https://github.com/YoussefHanouch',text:'github'},
    {logo: 'logo-Linkedin',link:'https://www.linkedin.com/in/youssef-hanouch-b55805219',text:'linkedin'},
    

  ]
  return (
    <section id='contact' className='py-10 px-3 text-white'>
<div className='text-center mt-8'>
<h3 className='text-4xl font-semibold'>
  Contact <span className='text-cyan-600'>Me</span>
</h3>
<p className='text-gray-400 mt-3 text-lg '>Get in touch</p>
<div className='mt-16 flex md:flex-row flex-col gap-6 max-w-5xl bg-gray-800 md:p-6 p-2 rounded-lg mx-auto '>
<form className='flex flex-col flex-1 gap-5'>
  <input type="text" placeholder='Your Name ' />
  <input type="email" placeholder='Your Email Address' />
  <textarea placeholder="Your Message" rows={10}></textarea>
            <button className="btn-primary w-fit">Send Message</button>
</form>
<div className='flex flex-col gap-7'>
  {contact_information.map((contact,i)=>
  {
    return(
      <div
      key={i}
      className="flex flex-row  
        text-left gap-4 flex-wrap items-center"
    >

        <div className='min-w-[3.5rem]  text-2xl min-h-[3.5rem] flex items-center justify-center text-white bg-cyan-600 rounded-full '>
       
       
        <p className='md:text-base text-sm breaks-words' style={{fontSize:'120%'}}><a href={contact.link} target="_blank"> 
        <ion-icon   name={contact.logo}> </ion-icon>  
    </a>
    </p>
        </div> 
        <p className='md:text-base text-sm breaks-words'>{contact.text}</p>

        
         </div>
    )
  })}
   
</div>
</div>
</div>
    </section>
  )
}


export default Contact;
