import React from "react"
import Footer from "./Component/Footer"
import About from "./Component/About"
import Bot from "./Component/Bot"
import Skills from "./Component/Skills"
import Navbar from "./Component/Navbar"
import Contact from "./Component/Contact"
import SectionHero from "./Component/sectionHero"
import  Project  from "./Component/Project"
import ReactDOM from 'react-dom/client';
import '../src/index.css';
import Design  from "./Component/Particles"

const root = ReactDOM.createRoot(document.getElementById('root'));
const backgroundImageStyle = {
  backgroundImage: 'url("https://raw.githubusercontent.com/noorjsdivs/particlesjs/master/public/bannerBg.jpg")',
  backgroundSize: 'cover', // Adjust as needed
  backgroundRepeat: 'no-repeat', // Adjust as needed
  backgroundAttachment: 'fixed', // This will make the background fixed while scrolling
};
function App() {
 

  return ( <div style={backgroundImageStyle}>
    <Navbar/> 
    <SectionHero/>
    <About/>
    <Skills/>
    <Project/>
    
    <Contact/>
   
    <Bot/>
    
    <Footer/>
    <div className="w-full h-screen absolute top-0 left-0">
        <Design />
      </div>
    
   </div>
  )
}



   
  
root.render(
       <App /> 
);

